<template>
  <div class="content-body">
    <a-form :form="form" @submit="handleSubmit">
      <div class="body-box">
        <div class="body-module">
          <div class="login-panel">
            <!-- 1 -->
            <a-form-item>
              <div class="login-panel-title" autofocus>MONSTER</div>
            </a-form-item>
            <!-- 2 -->

            <div class="validate-register">
              <a-form-item has-feedback>
                <a-tooltip placement="rightTop" :trigger="['focus']">
                  <template slot="title">
                    <div class="tooltip-register">
                      <li>&middot;5-25</li>
                      <li>&middot;/</li>
                    </div>
                  </template>

                  <a-input
                    placeholder=""
                    autocapitalize="on"
                    class="login-panel-user"
                    v-decorator="[
                      'username',
                      {
                        rules:[{validator:checkUsername}]
                      }
                      ]"
                  ></a-input>
                </a-tooltip>
              </a-form-item>
            </div>
            <!-- 3 -->
            <div class="validate-register">
              <a-form-item has-feedback>
                <a-tooltip placement="rightTop" :trigger="['focus']">
                  <template slot="title">
                    <div class="tooltip-register">
                      <li>&middot;6-20</li>
                      <li>&middot;</li>
                      <li>&middot;</li>
                    </div>
                  </template>
                  <a-input
                    placeholder=""
                    type="password"
                    class="login-panel-password"
                    v-decorator="[
                      'userpass',
                      {
                        rules:[{required: true,pattern: regular.userpass,message: regular.userpassMsg},{validator: validateToNextPassword}]
                      }
                      ]"
                  ></a-input>
                </a-tooltip>
              </a-form-item>
            </div>
            <!-- 3 -->
            <div class="validate-register">
              <a-form-item has-feedback>
                <a-tooltip placement="rightTop" :trigger="['focus']">
                  <template slot="title">
                    <div class="tooltip-register">
                      <li>&middot;</li>
                    </div>
                  </template>
                  <a-input
                    placeholder=""
                    type="password"
                    class="login-panel-password"
                    v-decorator="[
                      'confirmpass',
                      {
                        rules:[{required: true,message:''},{validator:compareToFirstPassword}]
                      }
                      ]"
                  ></a-input>
                </a-tooltip>
              </a-form-item>
            </div>
            <!-- 4 -->
            <a-button type="primary" html-type="submit" class="login-panel-button-primary"></a-button>
          </div>
        </div>
      </div>
    </a-form>
  </div>
</template>

<script>
import { regular } from '../utils/validate.js'
export default {
  name: 'register',
  data() {
    return {
      regular: regular,
      form: this.form
    }
  },
  beforeCreate() { // vuebeforeCreate,form
    this.form = this.$form.createForm(this, {
      onFieldsChange: (_, changedFields) => { // 
        this.$emit('change', changedFields)
      },
      mapPropsToFields: () => {
        return {

          username: this.$form.createFormField({
            value: this.username
          })
        }
      },
      onValuesChange: (_, values) => {
        console.log(values)
        // Synchronize to vuex store in real time
        // this.$store.commit('update', values)
      }
    })
  },
  methods: {
    handleSubmit(e) { // 
      e.preventDefault()
      this.form.validateFields((err, values) => { // 
        if (!err) { // 
          // 
          // 
          this.confirmRegister(values)
        }
      })
    },
    compareToFirstPassword(rule, value, callback) { // 
      const form = this.form
      if (value && value !== form.getFieldValue('userpass')) {
        callback(new Error(''))
      } else {
        callback()
      }
    },
    validateToNextPassword(rule, value, callback) { // 
      const form = this.form
      if (value) {
        form.validateFields(['confirmpass'], { force: true })
      }
      callback()
    },
    checkaccount() {
      const username = this.form.getFieldValue('username')
      const form = this.form
      const usererror = form.fieldsStore.fields.username.errors

      if (!username | username === '') {
        console.log('')
      } else {
        if (!usererror) {
          // 
          this.axios(
            {
              url: 'checkregister',
              method: 'post',
              data: {
                username: username,
                identitytype: 'account'
              },
              transformRequest: [function (data) {
                // Do whatever you want to transform the data
                let ret = ''
                for (let it in data) {
                  //  
                  ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                }
                return ret
              }],
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              }
            })
            .then(function (response) {
              // 
              form.validateFields(['username'], { force: true })
            })
            .catch(function (error) {
              console.log(error)
              // 
            })
        }
      }
    },
    checkUsername(rules, value, callback) {
      if (!value | value === '') { callback(new Error(this.regular.usernameMsg)) } else {
        const reg = new RegExp(this.regular.username)
        if (!reg.test(value)) {
          console.log(this.form)
          callback(new Error(this.regular.usernameMsg))
        } else {
          console.log(rules)
          callback()
        }
      }
    },
    confirmRegister(values) {
      const router = this.$router
      const form = this.form
      this.axios(
        {
          url: 'register',
          method: 'post',
          data: {
            username: values.username,
            password: values.userpass,
            identitytype: 'account'
          },
          transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              //  
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function (response) {
          console.log(response)// 
          router.push({ name: 'registerOk', params: { username: values.username } })
        })
        .catch(function (error) {
          console.log(error)
          if (error.code === 466) { // 466
            // 
            form.validateFields(['username'], { force: true }, (errors, values) => {
              form.setFields({
                username: {
                  value: values.username,
                  errors: [new Error(error.msg)]
                }
              })
            })
          }
        })
    }
  }
}
</script>

  <style  lang = "less" scoped>
@import "../less/register.less";
</style>
