<template>
  <div class="content-body">
    <a-form :form="form" @submit="handleSubmit">
      <a-divider orientation="left">
        <h4></h4>
      </a-divider>
      <a-row type="flex" justify="start">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-decorator="['nickname']" v-bind="formItemLayout">
            <label v-decorator="['nickname']">{{this.form.getFieldValue('nickname')}}</label>
          </a-form-item>
        </a-col>
        <a-col :span="0">
          <a-form-item>
            <a-input v-decorator="['id']"></a-input>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-radio-group v-decorator="['sex']">
              <a-radio :value="1"></a-radio>
              <a-radio :value="0"></a-radio>
            </a-radio-group>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start" :gutter="1">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-date-picker v-decorator="['birthday']" style="width: 100%" placeholder="" />
          </a-form-item>
        </a-col>
      </a-row>

      <a-divider orientation="left">
        <h4></h4>
      </a-divider>
      <a-row type="flex" justify="start" :gutter="1">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-input v-decorator="['phone']" placeholder=""></a-input>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="9">
          <a-form-item v-bind="formItemLayout">
            <a-input v-decorator="['email']" placeholder=""></a-input>
          </a-form-item>
        </a-col>
      </a-row>

      <a-row type="flex" justify="start" :gutter="2">
        <a-col :span="3">
          <a-form-item label="" v-bind="labelItemLayout"></a-form-item>
        </a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-cascader
              v-decorator="['province']"
              :fieldNames="{ label: 'cname', value: 'id', children: 'items' }"
              :options="province"
              @change="provinceChange"
              placeholder="----"
              ref="province"
            ></a-cascader>
          </a-form-item>
        </a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-cascader
              v-decorator="['region']"
              :fieldNames="{ label: 'cname', value: 'id', children: 'items' }"
              :options="region"
              @change="regionChange"
              placeholder="----"
              ref="region"
            ></a-cascader>
          </a-form-item>
        </a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-cascader
              v-decorator="['city']"
              :fieldNames="{ label: 'cname', value: 'id', children: 'items' }"
              :options="city"
              @change="cityChange"
              placeholder="----"
              ref="city"
            ></a-cascader>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row type="flex" justify="start">
        <a-col :span="3"></a-col>
        <a-col :span="3">
          <a-form-item v-bind="formItemLayout">
            <a-button type="primary" htmlType="submit" class="button-primary"></a-button>
          </a-form-item>
        </a-col>
      </a-row>
    </a-form>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  name: 'userinfo',
  data() {
    console.log(1)
    return {
      formItemLayout: {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          }        }
      }, labelItemLayout: {
        labelCol: {
          xs: {
            span: 24,
            offset: 0,
          }        }
      },
      province: [],
      region: [],
      city: [],
      aa: false
    }
  },
  beforeCreate() { // vuebeforeCreate,form
    const _this = this
    _this.form = _this.$form.createForm(_this, {
      onFieldsChange: (_, changedFields) => { // 
        _this.$emit('change', changedFields)
      },
      mapPropsToFields: () => {
        return {
          // phone: this.$form.createFormField({
          //   value: this.phone
          // })
        }
      },
      onValuesChange: (_, values) => {
        // console.log(values)
      }
    })
  },
  mounted() {
    const _this = this
    // 
    const id = JSON.parse(this.$store.getters.userinfo).id

    let p1 =
      _this.axios( // 
        {
          url: '/users/' + id,
          method: 'get'        })
        .then(function (response) {
          let { id, nickname, sex, phone, email, birthday, province, region, city } = response.data
          birthday = moment(birthday, 'YYYY-MM-DD')
          _this.form.setFieldsValue({ id, nickname, sex, phone, email, birthday })
          return { province, region, city }
        }).catch(function (error) {
          return error
        })

    let p2 = Promise.race([p1]).then((result) => {
      // console.log('', result)
      _this.axios.get( // 
        '/syscity', {
          params: { pid: 0, limit: 100, offset: 0 }
        })
        .then(function (response) {
          console.log('')
          console.log(response.data)
          _this.province = response.data.rows
          _this.$refs.province.setValue([result.province])
        }).catch(function (error) {
          console.log(error)
        })
      return result
    }).catch((error) => {
      console.log(error)  //  'failed'
    })
    let p3 = Promise.race([p2]).then((result) => {
      // console.log('', result)
      _this.axios.get( // 
        '/syscity', {
          params: { pid: result.province, limit: 100, offset: 0 }
        })
        .then(function (response) {
          _this.region = response.data.rows
          _this.$refs.region.setValue([result.region])
        }).catch(function (error) {
          console.log(error)
        })
      return result
    }).catch((error) => {
      console.log(error)  //  'failed'
    })
    Promise.race([p3]).then((result) => {
      // console.log('', result)
      _this.axios.get( // 
        '/syscity', {
          params: { pid: result.region, limit: 100, offset: 0 }
        })
        .then(function (response) {
          _this.city = response.data.rows
          _this.$refs.city.setValue([result.city])
        }).catch(function (error) {
          console.log(error)
        })
    }).catch((error) => {
      console.log(error)  //  'failed'
    })
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => { // 
        if (!err) { // 
          // 
          // 
          this.confirmRegister(values)
        }
      })
    },
    moment,
    provinceChange(value) {

      const _this = this
      // console.log(value[0])
      //value
      _this.$refs.region.setValue([undefined])
      _this.$refs.city.setValue([undefined])

      _this.axios.get( // 
        '/syscity', {
          params: { pid: value[0], limit: 100, offset: 0 }
        })
        .then(function (response) {
          // console.log('')
          // console.log(response.data)
          _this.region = response.data.rows
          // _this.$refs.region.setValue([response.data.rows[0].id])
        }).catch(function (error) {
          console.log(error)
        })
    },
    regionChange(value) {

      this.$refs.city.setValue([undefined])
      this.axios.get( // 
        '/syscity', {
          params: { pid: value[0], limit: 100, offset: 0 }
        })
        .then((response) => {
          this.city = response.data.rows
          // _this.$refs.city.setValue([response.data.rows[0].id])
        }).catch(function (error) {
          console.log(error)
        })
    },
    cityChange(value) {
    },
    confirmRegister(values) {
      this.axios.patch(
        '/users/' + values.id, { // 
          sex: values.sex,
          birthday: values.birthday.format('YYYY-MM-DD'),
          phone: values.phone,
          email: values.email,
          province: values.province[0],
          region: values.region[0],
          city: values.city[0]
        }
      ).then((response) => {
        console.log(response)// 
        this.$Modal.success({
          title: '',
          content: ''
        })
      })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>
